import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router-dom'

import ArrowBackIcon from '@mui/icons-material/ArrowBack'
import DeleteIcon from '@mui/icons-material/Delete'
import EditIcon from '@mui/icons-material/Edit'
import {
  Box,
  Button,
  Card,
  Checkbox,
  Container,
  Grid,
  Modal,
  Popover,
  Stack,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField,
  Tooltip,
  Typography
} from '@mui/material'
import { LocalizationProvider, MobileDatePicker } from '@mui/x-date-pickers'
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs'

import { DEFAULT_ATTENDANCE_COLUMNS } from 'src/data/attenndance'
import { CONFERENCE_MESSAGE } from 'src/data/messages'

import type { ConferenceAttendance, SingleConference } from 'src/models/conference'
import { type DateProps, defaultDate } from 'src/models/date'

import CancelSubmitBtns from 'src/components/CancelSubmitBtns'

import RoundDateModal from 'src/content/manage/Conference/Attendance/RoundDateModal'

import {
  useCreateConferenceRound,
  useDeleteConferenceRound,
  useGetSingleConference,
  usePatchConferenceRound,
  usePutConferenceAttendance
} from 'src/hooks/api/clubConferenceHooks'
import useSafeAuthUser from 'src/hooks/useSafeAuthUser'

const Attendance: React.FC = () => {
  const [attendanceDate, setAttendanceDate] = useState<Date>(new Date())
  const [isAddCalendarOpen, setIsAddCalendarOpen] = useState(false)
  const [editModalId, setEditModalId] = useState<number | null>(null)
  const [popOverId, setPopOverId] = useState<number | null>(null)
  const [deleteModalId, setDeleteModalId] = useState<number | null>(null)
  const [anchorEl, setAnchorEl] = useState<HTMLButtonElement | null>(null)
  const [mutationTarget, setMutationTarget] = useState<number>(-1)
  const navigate = useNavigate()
  const { id, confId } = useParams()
  const clubId = Number(id)
  const conferenceId = Number(confId)
  const { userInfo } = useSafeAuthUser()
  const { enqueueSnackbar } = useSnackbar()

  const [conference, setConference] = useState<SingleConference>({
    id: conferenceId,
    title: '',
    participantTarget: '',
    conferenceParticipants: [],
    conferenceRounds: []
  })

  const [attendances, setAttendances] = useState<Pick<ConferenceAttendance, 'id' | 'attendanceType'>[]>([])
  const club = userInfo?.clubs.find(club => club.id === clubId)
  const clubDate: DateProps = { year: club?.year || defaultDate.year, semester: club?.semester || defaultDate.semester }

  const { data, isSuccess } = useGetSingleConference(clubId, clubDate, conferenceId)

  const addNewRoundMutation = useCreateConferenceRound({
    clubId,
    conferenceId,
    startAt: attendanceDate,
    doesAllMemberAttended: true
  })
  const patchRoundMutation = usePatchConferenceRound()
  const deleteRoundMutation = useDeleteConferenceRound()
  const putAttendanceMutation = usePutConferenceAttendance({ clubId, conferenceId, statusOfAttendances: attendances })

  const handleClickNavigateBack: React.MouseEventHandler<HTMLButtonElement> = () => {
    navigate(-1)
  }

  const handleCheckAttendance = (userId: number, roundId: number, attendanceType: 1 | 0 | 2, attendanceId: number) => {
    setConference(prev => {
      return {
        ...prev,
        conferenceParticipants: prev?.conferenceParticipants.map(row =>
          row.user.id === userId
            ? {
                ...row,
                attendances: row.attendances.map(round =>
                  round.round === roundId ? { ...round, attendanceType: attendanceType === 1 ? 0 : 1 } : round
                ),
                attendanceCount: attendanceType === 1 ? row.attendanceCount - 1 : row.attendanceCount + 1
              }
            : row
        )
      } as SingleConference
    })
    setAttendances(prev => {
      return prev.map(attendance =>
        attendance.id === attendanceId ? { ...attendance, attendanceType: attendanceType === 1 ? 0 : 1 } : attendance
      )
    })
  }
  const handleSetAttendance = (date: Date | null) => {
    if (date === null) return
    setAttendanceDate(date)
  }
  const handleAttendanceMutation = (mutationType: 'post' | 'patch', date: Date | null) => {
    if (date === null || (mutationType === 'patch' && mutationTarget === -1)) {
      enqueueSnackbar(CONFERENCE_MESSAGE.WRONG_DATE, { variant: 'error' })
      return
    }
    if (
      conference.conferenceRounds.some(
        round => new Date(round.startAt).toDateString() === new Date(date).toDateString()
      ) // info 이미 해당 날짜에 회차 존재하는 경우 핸들링
    ) {
      enqueueSnackbar(CONFERENCE_MESSAGE.EXIST_ROUND_DATE, { variant: 'error' })
      return
    }
    mutationType === 'post'
      ? addNewRoundMutation.mutate()
      : patchRoundMutation.mutate({
          clubId,
          conferenceId,
          conferenceRoundId: mutationTarget,
          startAt: date,
          note: '',
          isPublic: false
        })
    handleClosePopover()
  }

  const handleEditModalClose = () => setEditModalId(null)
  const handleAddCalendarOpen = () => setIsAddCalendarOpen(!isAddCalendarOpen)

  const handleOpenPopover = (event: React.MouseEvent<HTMLButtonElement>) => {
    setMutationTarget(parseInt(event.currentTarget.id))
    setPopOverId(parseInt(event.currentTarget.id))
    setAnchorEl(event.currentTarget)
  }

  const handleClosePopover = () => {
    setAnchorEl(null)
    setPopOverId(null)
  }

  const open = Boolean(anchorEl)

  const handleDeleteModalOpen = (roundId: number) => {
    setDeleteModalId(roundId)
  }
  const handleDeleteRound = (conferenceRoundId: number) => {
    deleteRoundMutation.mutate({
      clubId,
      conferenceId,
      conferenceRoundId
    })
  }

  const handleSaveAttendances = () => {
    putAttendanceMutation.mutate()
  }
  useEffect(() => {
    if (isSuccess) {
      setConference(data)
      setAttendances(data.conferenceParticipants.map(participant => participant.attendances).flat())
    }
  }, [data])
  return (
    <Box m={3}>
      <Helmet>
        <title>출석 체크</title>
      </Helmet>
      <Container>
        <Card>
          <Box display={'flex'} alignItems={'center'} p={1}>
            <Button onClick={handleClickNavigateBack} color="primary">
              <ArrowBackIcon />
            </Button>
            <Typography variant="h4" component="h4">
              회차 및 출석 관리
            </Typography>
            <Typography variant="body1" ml={1}>
              신규회차는 기본적으로 전원 출석 상태로 추가됩니다.
            </Typography>
          </Box>
          <Grid container sx={{ dislplay: 'flex', flexDirection: 'row' }}>
            <Grid item lg={12} md={12} sm={12}>
              <TableContainer sx={{ overflow: 'auto' }}>
                <Table>
                  <TableHead>
                    <TableRow hover>
                      {DEFAULT_ATTENDANCE_COLUMNS.map(column => (
                        <TableCell align="center" sx={{ flex: 1, minWidth: 100 }} key={column.label}>
                          {column.headerName}
                        </TableCell>
                      ))}
                      {conference?.conferenceRounds.map(column => (
                        <TableCell
                          align="center"
                          sx={{
                            flex: 1,
                            justifyContent: 'center',
                            alignItems: 'center',
                            minWidth: 100,
                            '&:hover': { backgroundColor: '#E4E4E4', cursor: 'pointer' }
                          }}
                          key={column.id.toString()}
                        >
                          <Tooltip
                            title="클릭하면 삭제 혹은 수정을 할 수 있습니다."
                            placement="top"
                            id={column.id.toString()}
                          >
                            <Box
                              id={column.id.toString()}
                              component={Button}
                              onClick={handleOpenPopover}
                              sx={{
                                display: 'flex',
                                flexDirection: 'column',
                                justifyContent: 'center',
                                alignItems: 'center',
                                color: '#636F86',
                                padding: 0,
                                width: '100%',
                                '&:hover': { backgroundColor: '#E4E4E4', cursor: 'pointer' }
                              }}
                            >
                              <Typography fontWeight="700">{column.round} 회차</Typography>
                              <Typography fontWeight="700">
                                {new Date(column.startAt).getMonth() + 1}월 {new Date(column.startAt).getDate()}일
                              </Typography>
                            </Box>
                          </Tooltip>
                          <Popover
                            id={column.id.toString()}
                            open={open && popOverId === column.id}
                            anchorEl={anchorEl}
                            onClose={handleClosePopover}
                            anchorOrigin={{
                              vertical: 'bottom',
                              horizontal: 'left'
                            }}
                            transformOrigin={{
                              vertical: 'top',
                              horizontal: 'left'
                            }}
                          >
                            <Box sx={{ display: 'flex', flexDirection: 'row', padding: 1, alignItem: 'center' }}>
                              <Button onClick={() => handleDeleteModalOpen(column.id)}>
                                <DeleteIcon color="error" />
                              </Button>
                              <Button onClick={() => setEditModalId(column.id)}>
                                <EditIcon color="success" />
                              </Button>
                            </Box>
                          </Popover>
                          <Modal
                            open={deleteModalId === column.id}
                            onClose={() => setDeleteModalId(null)}
                            aria-labelledby="delete-modal-title"
                            aira-describedby="delete--modal-description"
                          >
                            <Box
                              sx={{
                                position: 'absolute',
                                top: '50%',
                                left: '50%',
                                transform: 'translate(-50%, -50%)',
                                display: 'flex',
                                flexDirection: 'column',
                                justifyContent: 'center',
                                alignItems: 'center',
                                background: '#ffffff',
                                minWidthidth: 483,
                                minHeight: 132,
                                borderRadius: 1,
                                p: 2
                              }}
                            >
                              <Stack direction="column" alignItems="center">
                                <Typography variant="h4" color="primary">
                                  정말 삭제하시겠습니까? 한 번 삭제하신 내역은 돌이킬 수 없습니다.
                                </Typography>
                                <Typography variant="body1" color="primary">
                                  해당 회차의 출석 내용도 삭제되며, 회차 순서가 재정렬됩니다.
                                </Typography>
                              </Stack>
                              <Box
                                sx={{
                                  display: 'flex',
                                  flexDirection: 'row',
                                  gap: 5,
                                  mt: 2,
                                  justifyContent: 'center',
                                  alignItem: 'center'
                                }}
                              >
                                <Button variant="outlined" color="primary" onClick={() => setDeleteModalId(null)}>
                                  취소
                                </Button>
                                <Button variant="contained" color="error" onClick={() => handleDeleteRound(column.id)}>
                                  삭제
                                </Button>
                              </Box>
                            </Box>
                          </Modal>
                          <RoundDateModal
                            label={`${column.round}회차 일정`}
                            open={column.id === editModalId}
                            onClose={handleEditModalClose}
                            currentRoundDate={column.startAt}
                            handleMutateAttendance={handleAttendanceMutation}
                          />
                        </TableCell>
                      ))}
                      <TableCell align="center">
                        <Button onClick={handleAddCalendarOpen}>회차 추가</Button>
                        <LocalizationProvider dateAdapter={AdapterDayjs}>
                          <MobileDatePicker
                            label="신규 회차 일정"
                            open={isAddCalendarOpen}
                            onClose={handleAddCalendarOpen}
                            value={attendanceDate}
                            onChange={handleSetAttendance}
                            onAccept={() => handleAttendanceMutation('post', attendanceDate)}
                            renderInput={params => <TextField sx={{ display: 'none' }} {...params} />}
                          />
                        </LocalizationProvider>
                      </TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {conference?.conferenceParticipants?.map(row => (
                      <TableRow hover key={row.user.id}>
                        <TableCell align="center" sx={{ flex: 1, minWidth: 100 }}>
                          {row.user.name}
                        </TableCell>
                        <TableCell align="center" sx={{ flex: 1, minWidth: 100 }}>
                          {row.user.level}
                        </TableCell>
                        <TableCell align="center" sx={{ flex: 1, minWidth: 100 }}>
                          {row.user.studentId}
                        </TableCell>
                        <TableCell align="center" sx={{ flex: 1, minWidth: 100 }}>
                          {row.user.department}
                        </TableCell>
                        <TableCell align="center" sx={{ flex: 1, minWidth: 100 }}>
                          {row.user.note === '' ? '-' : row.user.note}
                        </TableCell>
                        <TableCell align="center" sx={{ flex: 1, minWidth: 100 }}>
                          {row.totalAttendanceCount !== 0 ? `${row.attendanceCount}/${row.totalAttendanceCount}` : '-'}
                        </TableCell>
                        {row.attendances.map(attendance => (
                          <TableCell align="center" sx={{ flex: 1, minWidth: 100 }} key={attendance.id}>
                            <Checkbox
                              checked={attendance.attendanceType === 1}
                              onChange={() =>
                                handleCheckAttendance(
                                  row.user.id,
                                  attendance.round,
                                  attendance.attendanceType,
                                  attendance.id
                                )
                              }
                            />
                          </TableCell>
                        ))}
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
            </Grid>
          </Grid>
          <CancelSubmitBtns handleSubmit={handleSaveAttendances} submitBtnText="저장" />
        </Card>
      </Container>
    </Box>
  )
}

export default Attendance
