import { useState } from 'react'

import { TextField } from '@mui/material'
import { LocalizationProvider, MobileDatePicker } from '@mui/x-date-pickers'
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs'

interface RoundDateModalProps {
  label: string
  open: boolean
  onClose: () => void
  currentRoundDate: Date
  handleMutateAttendance: (mutationType: 'post' | 'patch', date: Date | null, currentDate?: Date) => void
}
const RoundDateModal = ({ label, open, onClose, currentRoundDate, handleMutateAttendance }: RoundDateModalProps) => {
  const [value, setValue] = useState(currentRoundDate)
  return (
    <LocalizationProvider dateAdapter={AdapterDayjs}>
      <MobileDatePicker
        label={label}
        open={open}
        onClose={onClose}
        value={value}
        onChange={(date: Date | null) => setValue(date ?? currentRoundDate)}
        onAccept={(date: Date | null) => handleMutateAttendance('patch', date)}
        renderInput={params => <TextField sx={{ display: 'none' }} {...params} />}
      />
    </LocalizationProvider>
  )
}

export default RoundDateModal
